import { useContext } from "react";
import { useParams } from "react-router-dom";
import { modeContext } from "../../../Home";
import { experienceData, goExperienceData, pyExperienceData } from "../../data";
import { CompanyName, Duration, EmptyCell, Position } from "./ExperienceUtils";

const DescriptionHeader = () => <span className="w-full border-b border-orange-500 p-1 mb-1 ">Responsibilities</span>

const FirstEmptyBlock = ({responsibility}) => (
    <div className="w-[48%] h-fit overflow-y-scroll shadow-lg">
        <div className="h-fit w-fit p-2 flex flex-col justify-center items-start border shadow-xl rounded border-orange-400">
            <DescriptionHeader />
            <ol className="font-light text-[0.8rem] leading-6 tracking-wide">
                {responsibility.map((point, idx) => <li key={idx} className="">{idx+1}. {point}</li>)}
            </ol>
        </div>
    </div>
);

const CenterLine = ({givenHeight = false}) => {
    const afterStyles = 'after:absolute after:top-0 after:-m-2 after:bg-red-300 after:p-1 after:rounded-full after:rotate-45 after:border-4 after:border-red-300 after:content-[""]';
    return (
        <div className={`relative h-full mx-4 flex flex-row justify-start`}>
            <div className={`${givenHeight ? `h-24` : 'h-full'} border-l-2 h-full border-red-300 border ${afterStyles}`}></div>
        </div>
    )
};

const InformationBlock = ({text, name, location, role, duration}) => {
    const {dark} = useContext(modeContext);
    return (
        <div className={`group/edu h-fit flex flex-col p-4 border ${dark ? 'border-slate-500' : ''} leading-6 tracking-wide shadow-lg rounded-lg transition duration-700 cursor-pointer mobile:bg-white-200 mobile:p-2`}>
            <CompanyName text={text} name={name} location={location} />
            <Position text={text} position={role} />
            <Duration text={text} duration={duration} />
        </div>
    )
};

const AcademicDiv = ({...props}) => {
    return (
        <div className='h-80 w-[90%] even:flex-row-reverse flex flex-row justify-center items-start p-0'>
            <FirstEmptyBlock {...props} />
            <CenterLine />
            <div className={`flex flex-row w-[48%] h-full ${props.index % 2 === 0 ? 'justify-end' : 'justify-start'}`}>
                <InformationBlock {...props} />
            </div>
        </div>
    )
}

const DesktopView = ({text}) => {
    const {type} = useParams();
    const {dark} = useContext(modeContext);
    const afterStyles = `after:content-[''] after:absolute after:right-0 after:p-[7px] ${dark ? 'after:border-gray-400': 'after:border-cyan-500'} after:border-t-2 after:border-r-2  after:rotate-45`
    const experienceData = type === 'go' ? goExperienceData : pyExperienceData;
    return (
        <div className='flex flex-col h-full p-2 transition duration-700 mobile:hidden'>
            <div className={`w-[90%] h-full flex flex-col justify-start items-center mx-auto rounded-xl shadow-xl p-10 ${dark ? 'shadow-3xl shadow-slate-800 bg-slate-900' : 'bg-white'} z-10`}>
                <CenterLine givenHeight={true} />
                {experienceData.map((obj, idx) => { return <AcademicDiv key={idx} index={idx} text={text} name={obj.name} location={obj.location} role={obj.role} duration={obj.duration} responsibility={obj.responsiblity}  /> })}
            </div>
        </div>
    )
};

export default DesktopView;


