import { BrowserRouter, Routes, Route, Navigate, useParams } from "react-router-dom";

import Profile from './components/Profile';
import Layout from './components/Layout';
import Feedbacks from './components/Feedbacks';
import NoPage from './components/NoPage';
import Home from "./Home";
import ProtectedRoute from "./ProtectedRoute";

// PYTHON: 16252081514; GOLANG: 715121147

const MainRoutes = () => {
    const type = localStorage.getItem("type")
    return (
    <BrowserRouter>
        <Routes>
            <Route path="/" element={<Layout />}>
                <Route path="/" element={<Navigate to={`/portfolio/${type ? type : "python"}`} replace />} />
                <Route path="/portfolio" element={<Navigate to={`/portfolio/${type ? type : "python"}`} replace />} />
                <Route path={`/portfolio/:type`} element={<ProtectedRoute />} >
                    <Route path={`/portfolio/:type`} element={<Home />} >
                        <Route path="profile" element={<Profile />} />
                    </Route>
                </Route>
                <Route path="*" element={<NoPage />} />
                <Route path="/admin" element={<Feedbacks />} />
            </Route>
        </Routes>
    </BrowserRouter>
    )
};

export default MainRoutes;