export const CompanyName = ({name, text, location}) => {
    return (
        <span style={{color: text}} className="group-hover/edu:text-black text-cyan-500 transition duration-700 text-lg text-zinc-700 font-normal small:text-sm mobile:text-sm">
           {name}<span style={{color: text}} className="text-[0.9rem] text-gray-500 font-normal small:text-[0.7rem] mobile:text-[0.75rem]">, {location}</span>
        </span>
    )
};

export const Position = ({text, position}) => {
    return (
        <span style={{color: text}} className="group-hover/edu:text-black transition duration-700 text-md text-gray-500 font-light small:text-[0.7rem] mobile:text-[0.75rem]">{position}</span>
    )
};

export const Duration = ({text, duration}) => {
    return (
        <span style={{color: text}} className="group-hover/edu:text-black transition duration-700 text-sm text-gray-500 font-light small:text-[0.5rem] mobile:text-[0.7rem]">{duration}</span>
    )
};

export const EmptyCell = () => <span className="flex flex-col p-4 mobile:bg-white-200 mobile:p-2"></span>