import { useEffect, useState } from "react";
import { motion } from 'framer-motion';

export default function ScrollingBall({dark, setOffset, prevPos}) { 
    let currScrollPos = window.scrollY - document.documentElement.clientHeight;
    let totalScrollHeight = document.body.offsetHeight - document.documentElement.clientHeight;
    let fraction = (document.documentElement.clientHeight/totalScrollHeight)*100
    let percentScrolled = Math.floor(((totalScrollHeight - currScrollPos)/totalScrollHeight)*100)
    let widthScrollPercent = 100+fraction - percentScrolled + "%";

    const [isScrolling, setIsScrolling] = useState(''); 

    useEffect(() => {
        let timeoutId;
        const onScroll = () => {
          setOffset((prev) => {
            prevPos.current = prev; // gives the prev position and assigns it to prevPos useRef variable
            return window.pageYOffset; // setting current new offset(curr position)
          })
          // set spinning animation on scroll and stop spinning on pause
          clearTimeout(timeoutId);
          timeoutId = setTimeout(() => {
            setIsScrolling('');
          }, 850);
          setIsScrolling(prevPos.current > window.pageYOffset ? 'animate-[spinreverse_1s_linear_infinite]' : 'animate-spin');
        };
        // clean up code
        window.removeEventListener('scroll', onScroll);
        window.addEventListener('scroll', onScroll, { passive: true });
        return () => {
            window.removeEventListener('scroll', onScroll);
            clearTimeout(timeoutId);
        }
    }, []);
    return (
        <div style={{backgroundColor: "inherit", width: "100%"}} className="overflow-hidden">
            <motion.div 
            transition={{duration:3}}
                style={{width: widthScrollPercent}} 
                className={`h-fit mt-0 text-right ${dark ? " bg-white-500 border-b border-b-[#9DB2BF]" : "border-b border-b-gray-400"} transition-all transform ease-linear duration-1000`}>
                <motion.i style={{color : dark ? "#D1D2D3":"black"}} className={`${dark ? "fas fa-baseball-ball": "fa fa-soccer-ball-o"} text-right ${isScrolling}`}></motion.i>
            </motion.div>
        </div>
    )
};