import { MoonIcon, SunIcon } from "../../Base/BaseIcons";

const handleMode = (setDark) => {
    setDark((state) => {
      localStorage.setItem("d", !state);
      return !state;
    });
  };

const ThemeIconBasedOnMode = ({ dark }) => (dark ? <MoonIcon /> : <SunIcon />);

const ThemeButon = ({ setDark, dark }) => {
  return (
    <button
      onClick={() => handleMode(setDark)}
      className="text-md font-light border-none hover:opacity-70 py-2"
    >
      Theme <ThemeIconBasedOnMode dark={dark} />
    </button>
  );
};

export default ThemeButon;