import { useContext } from "react";
import { modeContext } from "../../Home";

const TitleSpan = ({text, name}) => {
    const {dark} = useContext(modeContext);
    const [first, rest] = [name.slice(0,2), name.slice(2,)] ;
    return (
        <div className="flex pb-4">
            <span style={{ fontWeight: "bold", color: dark ? text : ''}} className="text-[2.2rem] font-bold text-[#27374D] small:text-[1.5rem] mobile:text-[2rem]">{first}</span>
            <span style={{color: text}} className="border-b-2 border-b-green-800 w-[70px] text-[2.2rem] text-[#526D82] font-normal small:text-[1.5rem] small:w-[60px] mobile:text-[2rem]">{rest}</span>
        </div>
    )
};

const DescriptionSpan = ({textContent, content}) => (
        <span style={{color: textContent}} className="text-md text-slate-500 text-center small:text-[1rem] mobile:text-[1rem] mobile:px-4">
            {content}
        </span>
    );

function Header({name, content}){
    const {dark} = useContext(modeContext);
    var text, textContent = '';
    if(dark){
        text = "#D1D2D3";
        textContent = "gray";
    }
    return (
            <div className="
            w-full h-full flex flex-row justify-center items-center
            small:w-full
            mobile:w-full flex flex-row justify-center items-center mx-auto
            tablet:w-[40%]">
                <div className="w-[30%] flex flex-col items-center space-y-0 mobile:w-full">
                    <TitleSpan text={text} name={name} />
                    <DescriptionSpan textContent={textContent} content={content} />
                </div>
                <div className="bg-red-400 flex flex-col justify-start" >
                    <i className={`fa fa-bookmark absolute top-0 mt-0 pt-0 pr-20 right-0 p-0 z-10 text-[3rem] mobile:text-[2rem] mobile:pr-10 ${name === 'Experience' ? 'text-cyan-800' : 'text-teal-800'} `}></i>
                </div>
            </div>
    )
}

export default Header;

