import { motion } from "framer-motion";
import { useState, useEffect, useRef } from "react";
import NameAsTitle from "../../components/sections/section1/NameAsTitle";
import NavButtons from "../../components/sections/section1/NavbarButtons";
import ScrollingBall from "../../components/sections/section1/ScrollingBall";
import ThemeButon from "../../components/sections/section1/ThemeButton";


function Topnav({ dark, setDark, apiurl, authorization }) {
  const [offset, setOffset] = useState(0);
  let display = useRef(0);
  let prevPos = useRef(0);

  const props = {
    dark: dark,
    setDark: setDark,
  };

  const screenProps = {
    setOffset: setOffset,
    prevPos: prevPos,
  };

  const handleScroll = () => {
    prevPos.current <= offset
      ? (display.current = "-50px")
      : (display.current = "0");
  };

  useEffect(handleScroll, [offset, display]);

  let bg = "bg-white";
  let text,
    textTitle = "";

  if (dark) {
    bg = "bg-slate-900";
    text = "#D1D2D3";
    textTitle = "#9DB2BF";
  }

  return (
    <>
      {/* For mobile and tablets */}
      <motion.div
        id = "navbar"
        style={{ top: display.current, color: text }}
        className={`${bg} hidden sticky transition z-20 small:flex mobile:flex tablet:flex flex-col justify-between items-center space-y-1 w-full`}
      >
        <TopnavMobileView {...props} {...screenProps} />
      </motion.div>

      {/* For Laptops and Desktops */}
      <motion.div
        style={{ top: display.current, color: text }}
        className={`${bg} h-full w-full sticky top-0 left-0 right-0 z-20 flex flex-col transition duration-150 ease-linear small:hidden mobile:hidden tablet:hidden`}
      >
        <TopnavDesktopView {...props} {...screenProps} />
      </motion.div>
    </>
  );
}

const TopnavMobileView = ({ ...props }) => {
  return (
    <>
      <div className={`flex flex-row justify-between items-center w-full space-x-4 pl-2 pr-2`}>
        <NameAsTitle dark={props.dark} />
        <span className="flex flex-row justify-between items-center space-x-4">
          <ThemeButon {...props} />
        </span>
      </div>
      <NavButtons {...props} />
      <ScrollingBall {...props} />
    </>
  );
};

const TopnavDesktopView = ({ ...props }) => {
  return (
    <>
      <div className="flex flex-row w-full justify-between items-center place-items-center space-x-5 px-20 py-2">
        <NameAsTitle dark={props.dark} />
        <NavButtons {...props} />
      </div>
      <ScrollingBall {...props} />
    </>
  );
};

export default Topnav;
